<template>
    <v-row class="justify-center">
        <v-col cols="12" md="10">
            <v-alert
                v-model="alert"
                :type="alertType"
                border="left"
                close-text="Close Alert"
                dismissible
                transition="scale-transition"
            >
                {{ alertMessage }}
            </v-alert>

            <v-dialog
                v-model="loadingActions"
                hide-overlay
                persistent
                transition="dialog-bottom-transition"
                width="300"
            >
                <v-card :color="getAppColors.primary" dark>
                    <v-card-text>
                        Processing...
                        <v-progress-linear
                            indeterminate
                            color="white"
                            class="mb-0"
                        ></v-progress-linear>
                    </v-card-text>
                </v-card>
            </v-dialog>

            <v-data-table
                :headers="headers"
                :items="records"
                :loading="loading"
                :options.sync="options"
                :server-items-length="totalRecords"
                class="elevation-4 rounded-lg mt-2"
                loading-text="Loading... Please wait"
                :footer-props="tableFooterProps"
                sort-by="depositDate"
                sort-desc
                must-sort
                v-show="!loadingActions"
            >
                <template v-slot:[`item.depositDate`]="{ item }">
                    {{ formatDateTime(item.depositDate) }}
                </template>
                <template v-slot:[`item.amount`]="{ item }">
                    {{ item.amount | currency(item.offsetDescription) }}
                </template>
                <template v-slot:[`item.offsetDescription`]="{ item }">
                    {{ item.offsetDescription | titleCase }}
                </template>
                <template v-slot:top>
                    <v-toolbar
                        color="green darken-2"
                        class="rounded-t-lg"
                        dark
                        flat
                    >
                        <v-toolbar-title>ACH Funding</v-toolbar-title>
                        <v-divider class="mx-4" inset vertical />
                        <v-text-field
                            v-model="search"
                            label="Search"
                            append-icon="search"
                            single-line
                            hide-details
                            clearable
                            dark
                            @keyup.enter="
                                datepickerDateRange = [];
                                initialize();
                            "
                            @click:append="
                                datepickerDateRange = [];
                                initialize();
                            "
                            @click:clear="initialize()"
                        />
                        <v-spacer />
                        <v-toolbar-title>
                            Transactions for {{ dateRangeDisplay }}
                        </v-toolbar-title>
                        <v-divider class="mx-4" inset vertical />
                        <v-menu
                            v-model="filterMenu"
                            :close-on-content-click="false"
                            offset-x
                            left
                            max-width="400px"
                        >
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn
                                    color="primary"
                                    rounded
                                    dark
                                    v-bind="attrs"
                                    v-on="on"
                                >
                                    <v-icon>mdi-filter</v-icon>
                                </v-btn>
                            </template>

                            <v-card>
                                <v-card-title> Filters</v-card-title>
                                <v-card-text>
                                    <v-row>
                                        <v-col cols="12">
                                            <v-select
                                                v-model="filterTypes"
                                                label="Transaction Types"
                                                :color="getAppColors.primary"
                                                item-color="green"
                                                append-icon="mdi-filter"
                                                :items="transactionTypes"
                                                multiple
                                                chips
                                                deletable-chips
                                                @change="saveFilterPreferences"
                                            />
                                        </v-col>
                                        <v-col cols="12">
                                            <v-menu
                                                v-model="datePickerMenu"
                                                :close-on-content-click="false"
                                                :nudge-right="40"
                                                transition="scale-transition"
                                                offset-y
                                                min-width="auto"
                                                v-if="useCustomDateRange"
                                            >
                                                <template
                                                    v-slot:activator="{
                                                        on,
                                                        attrs,
                                                    }"
                                                >
                                                    <v-text-field
                                                        v-model="dateRangeText"
                                                        label="Choose Date Range"
                                                        :color="
                                                            getAppColors.primary
                                                        "
                                                        append-icon="mdi-calendar"
                                                        readonly
                                                        v-bind="attrs"
                                                        v-on="on"
                                                    />
                                                </template>
                                                <v-date-picker
                                                    v-model="
                                                        datepickerDateRange
                                                    "
                                                    :color="
                                                        getAppColors.primary
                                                    "
                                                    range
                                                    @change="
                                                        search = '';
                                                        initialize();
                                                    "
                                                />
                                            </v-menu>
                                            <v-select
                                                v-model="quickDateRange"
                                                :color="getAppColors.primary"
                                                :items="
                                                    getBlockchypDateRangeChoices
                                                "
                                                item-color="green"
                                                single-line
                                                hide-details
                                                return-object
                                                @change="initialize"
                                                v-else
                                            />
                                        </v-col>
                                        <v-col cols="6">
                                            <v-switch
                                                v-model="useCustomDateRange"
                                                color="green"
                                                label="Custom Date Range"
                                            />
                                        </v-col>
                                    </v-row>
                                </v-card-text>
                            </v-card>
                        </v-menu>
                    </v-toolbar>
                </template>
                <template v-slot:no-data>
                    <v-alert prominent text type="info" class="ma-2">
                        <v-row align="center">
                            <v-col class="grow"> No records found</v-col>
                            <v-col class="shrink">
                                <v-btn
                                    color="green"
                                    class="my-2 white--text"
                                    @click="initialize"
                                >
                                    Retry
                                </v-btn>
                            </v-col>
                        </v-row>
                    </v-alert>
                </template>
            </v-data-table>
        </v-col>
    </v-row>
</template>

<script>
import { mapGetters } from 'vuex';
import axios from 'axios';
import accounting from 'accounting';
import moment from 'moment';

export default {
    name: 'AchFundingList',
    data() {
        return {
            datePickerMenu: false,
            loading: false,
            alert: false,
            alertMessage: '',
            alertType: 'success',
            search: '',
            filterMenu: false,
            filterTypes: ['Deposits', 'Discount'],
            transactionTypes: ['Deposits', 'Discount'],
            tableFooterProps: {
                showFirstLastPage: true,
            },
            headers: [
                { text: 'Deposit Date', align: 'left', value: 'depositDate' },
                { text: 'Amount', align: 'right', value: 'amount' },
                { text: 'Location', align: 'right', value: 'locationName' },
                {
                    text: 'Type',
                    align: 'center',
                    value: 'offsetDescription',
                    filter: (value, search, item) => {
                        if (!this.filterTypes.length) return true;
                        if (!item.offsetDescription) return false;

                        const filterTypesToUpperCase = this.filterTypes.map(
                            (filterType) => filterType.toUpperCase()
                        );

                        return filterTypesToUpperCase.includes(
                            item.offsetDescription.toUpperCase()
                        );
                    },
                },
                { text: 'Bank', align: 'left', value: 'bankName' },
                { text: 'Routing', align: 'left', value: 'bankRouting' },
                {
                    text: 'Bank Account',
                    align: 'left',
                    value: 'bankAccountNumber.mask',
                },
                { text: 'Reference', align: 'left', value: 'referenceNumber' },
            ],
            options: {},
            records: [],
            totalRecords: 0,
            useCustomDateRange: false,
            datepickerDateRange: [],
            quickDateRange: {
                text: 'Yesterday',
                value: 'yesterday',
                useForBlockchyp: true,
                days: null,
            },
            loadingActions: false,
            axiosSource: null,
        };
    },
    watch: {
        useCustomDateRange: function (newValue) {
            if (!newValue) {
                this.datepickerDateRange = [];
                this.initialize();
            }
        },
        options: {
            handler() {
                this.initialize();
            },
            deep: true,
        },
    },
    computed: {
        ...mapGetters([
            'getAppColors',
            'getInternalMxController',
            'getInternalEmployeePreferencesController',
            'getBlockchypDateRangeChoices',
        ]),
        dateRangeText() {
            return this.datepickerDateRange
                .map((date) => moment(date).format('MM/DD/YYYY'))
                .join(' through ');
        },
        getTotal() {
            const filterTypesToUpperCase = this.filterTypes.map((filterType) =>
                filterType.toUpperCase()
            );

            let totalAmount = 0;
            this.records.forEach((record) => {
                if (
                    !this.filterTypes.length ||
                    filterTypesToUpperCase.includes(
                        record.offsetDescription.toUpperCase()
                    )
                ) {
                    totalAmount += record.amount;
                }
            });

            return accounting.formatMoney(totalAmount);
        },
        dateRange() {
            let daysFromStart = null;
            let daysFromEnd = null;

            let quickStartDate = null;
            let quickEndDate = null;

            if (this.quickDateRange !== {}) {
                switch (this.quickDateRange.value) {
                    case 'today':
                        daysFromStart = 0;
                        daysFromEnd = 0;
                        break;
                    case 'yesterday':
                        daysFromStart = 1;
                        daysFromEnd = 1;
                        break;
                    default:
                        daysFromStart = this.quickDateRange.days;
                        daysFromEnd = 0;
                }

                quickStartDate = moment().subtract(daysFromStart, 'days');
                quickEndDate = moment().subtract(daysFromEnd, 'days');
            }

            let startDate =
                this.datepickerDateRange[0] !== undefined
                    ? this.datepickerDateRange[0]
                    : quickStartDate !== null
                    ? quickStartDate
                    : '';
            let endDate =
                this.datepickerDateRange[1] !== undefined
                    ? this.datepickerDateRange[1]
                    : quickEndDate !== null
                    ? quickEndDate
                    : '';

            if (startDate !== '') {
                startDate = moment(startDate)
                    .set({
                        hour: 0,
                        minute: 0,
                        second: 0,
                        millisecond: 0,
                    })
                    .toISOString();
            }

            if (endDate !== '') {
                endDate = moment(endDate)
                    .set({
                        hour: 23,
                        minute: 59,
                        second: 59,
                        millisecond: 999,
                    })
                    .toISOString();
            }

            return { startDate: startDate, endDate: endDate };
        },
        dateRangeDisplay() {
            return this.dateRangeText
                ? this.dateRangeText
                : this.quickDateRange.text;
        },
    },
    mounted() {
        this.getFilterPreferences();
    },
    methods: {
        openAlert(type, message) {
            this.alertType = type;
            this.alertMessage = message;
            this.alert = true;
        },
        async initialize() {
            this.loading = true;
            this.alert = false;

            const { sortBy, sortDesc, page, itemsPerPage } = this.options;
            const currentOffset = (page - 1) * itemsPerPage;

            let sorting = false;
            const sortByField = sortBy.length === 1 ? sortBy[0] : null;
            const sortByDesc = sortDesc.length === 1 ? sortDesc[0] : null;

            if (sortByField) {
                sorting = sortByField;

                if (sortByDesc) {
                    sorting += ':desc';
                }
            }

            console.log(sorting);

            try {
                if (this.axiosSource) this.axiosSource.cancel();
                this.axiosSource = axios.CancelToken.source();

                let response = await axios.get(
                    `${this.getInternalMxController + '/get_ach_funding'}`,
                    {
                        withCredentials: true,
                        crossDomain: true,
                        params: {
                            start_date: this.dateRange.startDate,
                            end_date: this.dateRange.endDate,
                            sort: sorting,
                            limit: itemsPerPage,
                            offset: currentOffset,
                        },
                        cancelToken: this.axiosSource.token,
                    }
                );

                this.loading = false;
                this.axiosSource = null;

                if (response.data.warning) {
                    this.openAlert('info', response.data.message);
                } else {
                    if (response.data.data.totalRecords > 0) {
                        this.records = response.data.data.records;
                        this.totalRecords = response.data.data.totalRecords;
                    } else {
                        console.log('No ach fundings found');
                        this.records = [];
                        this.totalRecords = 0;
                    }
                }
            } catch (error) {
                console.error('Could not get list of ach fundings');
                console.error(error);

                if (!error.toString() == 'Cancel') {
                    this.openAlert('error', error.toString());
                }

                this.loading = false;
                this.axiosSource = null;
                this.records = [];
            }
        },
        formatDateTime(rawDateTime) {
            return rawDateTime
                ? moment(rawDateTime).format('MM/DD/YYYY LT')
                : '';
        },
        formatDate(rawDateTime) {
            return rawDateTime ? moment(rawDateTime).format('MM/DD/YYYY') : '';
        },
        async saveFilterPreferences() {
            let formData = new FormData();
            formData.append(
                'ach_funding_filters',
                JSON.stringify(this.filterTypes)
            );

            try {
                let response = await axios.post(
                    `${
                        this.getInternalEmployeePreferencesController +
                        '/save_ach_funding_filters'
                    }`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    }
                );

                if (response.status === 200) {
                    console.log('Filter preferences updated');
                } else {
                    console.log('Unable to save filter preferences');
                }
            } catch (error) {
                console.error(error);
            }
        },
        async getFilterPreferences() {
            try {
                let response = await axios.get(
                    `${
                        this.getInternalEmployeePreferencesController +
                        '/get_ach_funding_filters'
                    }`,
                    {
                        withCredentials: true,
                        crossDomain: true,
                        params: {},
                    }
                );

                if (response.data) {
                    this.filterTypes = response.data;
                } else {
                    console.log('No filter preferences found');
                    this.filterTypes = ['Deposits', 'Discount'];
                }
            } catch (error) {
                console.error('Could not get filter preferences');
                console.error(error);
            }
        },
    },
    filters: {
        titleCase: (value) => {
            if (!value) return '';

            value = value.toString().toLowerCase().split(' ');

            for (let i = 0; i < value.length; i++) {
                value[i] = value[i][0].toUpperCase() + value[i].slice(1);
            }

            return value.join(' ');
        },
        currency: (value, transactionType) => {
            if (!value) return '0';

            let formattedValue = accounting.formatMoney(value);

            return transactionType === 'void' ||
                transactionType === 'refund' ||
                transactionType === 'reverse'
                ? '-' + formattedValue
                : formattedValue;
        },
    },
};
</script>
